

/*
3~24 characters
Field should contain only Latin letters, digits, underscore or dash characters
*/
const usernameRegexp=/[a-zA-Z0-9_-]{3,24}/;

export const CODEFORCES_USERNAME_REGEXP=new RegExp(`^${usernameRegexp.source}$`);

export const normalizeUsername=(username:string):string=>{
	const trimmedUsername=username.trim();
	return CODEFORCES_USERNAME_REGEXP.test(trimmedUsername)?trimmedUsername:""
}